@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');

* {
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
}

body {
    background-color: #111;
    color: #fff;
    margin: 0;
}

.lists {
    margin-top: -150px;
}

footer {
    margin: 50px 0;
    text-align: center;
}

a {
    color: #fff;
}

.loading {
    position: fixed;
    inset: 0;
    z-index: 99;
    background-color: #000;
    display: grid;
    place-content: center;
}