@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
* {
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
}

body {
    background-color: #111;
    color: #fff;
    margin: 0;
}

.lists {
    margin-top: -150px;
}

footer {
    margin: 50px 0;
    text-align: center;
}

a {
    color: #fff;
}

.loading {
    position: fixed;
    inset: 0;
    z-index: 99;
    background-color: #000;
    display: grid;
    place-content: center;
}
.movieRow {
    margin-bottom: 30px;
}

.movieRow h2 {
    margin: 0px 0px 0px 30px;
}

.movieRow--listarea {
    overflow-x: hidden;
    padding-left: 30px;
}

.movieRow--list {
    transition: all ease 0.2s;
}

.movieRow--item {
    display: inline-block;
    width: 200px;
    cursor: pointer;
}

.movieRow--item img {
    width: 100%;
    height: 300px;
}

.movieRow--item img {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    transition: all ease 0.2s;
}

.movieRow--list img:hover {
    -webkit-transform: scale(1);
            transform: scale(1);
}

.movieRow--before-btn, .movieRow--next-btn {
    position: absolute;
    width: 40px;
    height: 300px;
    background-color: rgba(0,0,0,0.6);
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    cursor: pointer;
    opacity: 0;
    transition: all ease 0.5s;
}

.movieRow--before-btn {
    left: 0;
}

.movieRow--next-btn {
    right: 0;
}

.movieRow:hover .movieRow--before-btn, 
.movieRow:hover .movieRow--next-btn {
    opacity: 1;
}

@media (max-width: 760px) {
    .movieRow--before-btn,
    .movieRow--next-btn {
        opacity: 1;
    }
}
.featured {
    height: 100vh;
}

.featured--vertical {
    width: inherit;
    height: inherit;
    background: linear-gradient(to top, #111 10%, transparent 90%);
}

.featured--horizontal {
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, #111 30%, transparent 70%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 70px 0 150px 30px;
}

.featured--name {
    font-size: calc(1em + 4.5vw);
    font-weight: bold;
}

.featured--info {
    font-size: 18px;
    font-weight: bold;
    margin-top: 15px;
}

.featured--points, .featured--year, .featured--seasons {
    display: inline-block;
    margin-right: 15px;
}

.featured--points {
    color: #46d369;
}

.featured--description{
    margin-top: 15px;
    font-size: 1.4em;
    color: #999;
    max-width: 35%;
}

.featured--buttons {
    margin-top: 15px;
}

.featured--watchbutton, .featured--mylistbutton {
    display: inline-block;
    font-size: 20px;
    font-weight: bold;
    padding: 12px 25px;
    border-radius: 5px;
    text-decoration: none;
    margin-right: 10px;
    transition: all ease-in-out 0.2s;
}

.featured--watchbutton:hover, .featured--mylistbutton:hover {
    opacity: 0.8;
}

.featured--watchbutton {
    background-color: #fff;
    color: #000;
}

.featured--mylistbutton {
    background-color: #333;
    color: #fff;
}

.featured--genres {
    margin-top: 15px;
    font-size: 18px;
    color: #999;
}

@media (max-width: 760px) {
   .featured--info {
       font-size: 16px;
   }

   .featured--description {
       font-size: 0.9em !important;
       max-width: 60%;
   }

   .featured--watchbutton, 
   .featured--mylistbutton {
       font-size: 0.9em;
   }

   .featured--genres {
       font-size: 0.8em;
   }
}
header {
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    right: 0;
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 60px;
    background: transparent;
    transition: all ease 0.2s;
}

header.black {
    background-color: #141414;
}

header img {
    width: 100%;
}

.header--logo {
    width: 98px;
}

.header--user {
    width: 40px;
    height: 40px;
    background-color: #ffffffcb;
    border-radius: 50%;
    padding: 2px;
}

@media (max-width: 700px) {
    header {
        padding: 2px 20px;
    }
}
