.movieRow {
    margin-bottom: 30px;
}

.movieRow h2 {
    margin: 0px 0px 0px 30px;
}

.movieRow--listarea {
    overflow-x: hidden;
    padding-left: 30px;
}

.movieRow--list {
    transition: all ease 0.2s;
}

.movieRow--item {
    display: inline-block;
    width: 200px;
    cursor: pointer;
}

.movieRow--item img {
    width: 100%;
    height: 300px;
}

.movieRow--item img {
    transform: scale(0.9);
    transition: all ease 0.2s;
}

.movieRow--list img:hover {
    transform: scale(1);
}

.movieRow--before-btn, .movieRow--next-btn {
    position: absolute;
    width: 40px;
    height: 300px;
    background-color: rgba(0,0,0,0.6);
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    cursor: pointer;
    opacity: 0;
    transition: all ease 0.5s;
}

.movieRow--before-btn {
    left: 0;
}

.movieRow--next-btn {
    right: 0;
}

.movieRow:hover .movieRow--before-btn, 
.movieRow:hover .movieRow--next-btn {
    opacity: 1;
}

@media (max-width: 760px) {
    .movieRow--before-btn,
    .movieRow--next-btn {
        opacity: 1;
    }
}